.login {
    display: grid;
    place-items: center;
    background-color: black;
    min-height: 100vh;
}

.login img {
    width: 50%;
}

.login a {
    padding: 20px;
    background-color: #1DB954;
    border-radius: 40px;
    font-weight: 700;
    color: white;
    text-decoration: none;
}